html,
body {
    height: 100%;
}

#wrapper {
    min-height: 100%;
}

.section-wrap {
    position: relative;
    min-height: 100vh;
}

.section-intro {
    position: relative;
    min-height: 100vh;
    display: flex;
    color: $white;
    align-items: center;
    font-size: 24px;
    overflow: hidden;
    padding: 50px 0;
    font-family: "Poppins", Arial;
    background-color: #1d124e;
    line-height: 1.375;
    background: linear-gradient(106.11deg, #0F1013 1.09%, #181B8E 100%);

    &.bg-intro {
        background: url(../images/img-tower-bg.png) no-repeat 60% 50% / cover;

        @media (max-width: $widescreen) {
            background-position: 60% 50%;
        }

        @media (max-width: $widescreen-s) {
            background-image: url(../images/img-tower-bg-mobile.png);
            background-position: 95% 50%;
        }

        @media (max-width: $desktop) {
            background-position: 88% 50%;
        }

        @media (max-width: $desktop) {
            background-position: 88% 50%;
        }

        @media (max-width: $tablet) {
            background: linear-gradient(106.11deg, #0F1013 1.09%, #181B8E 100%);
        }
    }

    @media (max-width: $widescreen) {
        font-size: 21px;
    }

    @media (max-width: $desktop) {
        padding: 70px 0;
        font-size: 16px;
    }

    .container-full {
        width: 100%;
    }

    .img-bg {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
        z-index: 1;
    }

    .intro-holder {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: $desktop) {
            display: block;
        }
    }

    .btn {
        pointer-events: all;
    }

    .text-box {
        width: 49.5%;
        position: relative;
        z-index: 1000;
        pointer-events: none;
        max-width: 525px;

        @media (max-width: $widescreen) {
            width: 50%;
            padding-right: 15px;
        }

        @media (max-width: $desktop) {
            width: 100%;
            max-width: 400px;
        }

        @media (max-width: $tablet) {
            max-width: 100%;
        }
    }

    .img-box {
        width: 40%;

        @media (max-width: $widescreen) {
            width: 50%;
        }

        @media (max-width: $desktop) {
            width: 100%;
            display: none;
        }

        img {
            margin: 0 auto;
        }
    }

    &.subpage {
        padding: 170px 0;
        min-height: 900px;

        @media (max-width: $widescreen) {
            min-height: inherit;
        }

        @media (max-width: $desktop) {
            padding: 120px 0;
            min-height: inherit;
        }

        @media (max-width: $tablet) {
            padding: 100px 0;
        }

        .text-box {
            width: 83%;
            max-width: 100%;

            @media (max-width: $desktop) {
                width: 100%;
            }

            p {
                max-width: 90%;

                @media (max-width: $desktop) {
                    max-width: 100%;
                }
            }
        }
    }

    &.portfolio-subpage {
        padding: 170px 0;
        min-height: 900px;

        @media (max-width: $widescreen) {
            min-height: inherit;
        }

        @media (max-width: $desktop) {
            padding: 120px 0;
            min-height: inherit;
        }

        @media (max-width: $tablet) {
            padding: 100px 0;
        }

        h2 {
            font-family: "DM Serif Display", "Poppins";
            color: $primary;
            letter-spacing: .07em;
            font-weight: 400;
            padding: 0 0 21px;
            margin: 0 0 20px;
            position: relative;
            text-transform: uppercase;

            strong {
                display: block;
                font-weight: 400;
                color: $white;
            }

            &:after {
                position: absolute;
                left: 0;
                width: 122px;
                height: 1px;
                background: #41A4FF;
                content: "";
                bottom: 0;
            }
        }

        .text-box {
            width: 42%;
            max-width: none;

            @media (max-width: $desktop) {
                width: 100%;
            }
        }

        .img-box {
            width: 53.45%;

            .img-corner-holder .img-corner-frame {
                display: block;
            }
        }
    }
}

.btn-holder {
    position: absolute;
    left: 0;
    bottom: 20px;
    text-align: center;
    z-index: 1100;
    width: 100%;
    transition: all .3s ease-in-out;

}

.btn-explore {
    border: 2px solid $white;
    width: 140px;
    height: 140px;
    border-radius: 50%;
    display: inline-flex;
    vertical-align: top;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: $white;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 20px;
    font-weight: 600;
    bottom: 20px;

    @media (max-width: $widescreen) {
        width: 120px;
        height: 120px;
        font-size: 18px;
    }

    @media (max-width: $desktop) {
        width: auto;
        height: auto;
        border: 0;
        border-bottom: 2px solid $white;
        padding: 5px 0;
        border-radius: 0;
        font-size: 12px;
    }

    @media (max-height: 870px) {
        width: auto;
        height: auto;
        border: 0;
        border-bottom: 2px solid $white;
        padding: 5px 0;
        border-radius: 0;
        font-size: 12px;
    }

    &:hover,
    &:focus {
        text-decoration: none;
        color: $primary;
        border-color: $primary;
    }
}

.section-business {
    position: relative;
    padding: 30px 0;
    background: #F1F4FA;
    text-align: center;

    h4 {
        color: $secondary;
        font-size: 20px;
    }

    ul {
        margin: -15px;
        padding: 0;
        list-style: none;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: center;

        @media (max-width: $tablet) {
            justify-content: space-around;
        }

        li {
            padding: 15px;
            max-width: 25%;

            @media (max-width: $tablet) {
                max-width: 50%;
            }
        }
    }
}

.section {
    position: relative;

    .section-frame {
        padding: 150px 0;
        position: relative;

        @media (max-width: $widescreen) {
            padding: 120px 0;
        }


        @media (max-width: $desktop) {
            padding: 80px 0;
        }

        @media (max-width: $tablet) {
            padding: 40px 0;
        }
    }

    &.pt-0 {
        .section-frame {
            padding-top: 0 !important;
        }
    }

    &.pb-0 {
        .section-frame {
            padding-bottom: 0 !important;
        }
    }

    &.p-0 .section-frame {
        padding: 0 !important;
    }


    .img-center {
        img {

            margin: 0 auto;
        }
    }

    &.bg-gray {
        .section-frame {
            background: #F1F4FA;
        }
    }

    &.inclined {
        &:before {
            position: relative;
            width: 0;
            height: 0;
            border-style: solid;
            margin-left: -10vw;
            display: block;
            content: "";
            border-width: 0 0 100px 110vw;
            border-color: transparent transparent $white transparent;

            @media (max-width: $widescreen) {
                border-bottom-width: 70px;
            }

            @media (max-width: $desktop) {
                border-bottom-width: 50px;
            }
        }

        &:after {
            position: relative;
            width: 0;
            display: block;
            height: 0;
            content: "";
            border-style: solid;
            margin-right: -10vw;
            border-width: 100px 110vw 0 0;
            border-color: $white transparent transparent transparent;

            @media (max-width: $widescreen) {
                border-top-width: 70px;
            }

            @media (max-width: $desktop) {
                border-top-width: 50px;
            }
        }

        &.bg-gray {
            &:before {
                border-color: transparent transparent rgba(#F1F4FA, .7) transparent;
            }

            &:after {
                border-color: rgba(#F1F4FA, .7) transparent transparent transparent;
            }
        }
    }
}

.section-head {
    margin: 0 0 60px;

    @media (max-width: $desktop) {
        margin: 0 0 40px;
    }

    @media (max-width: $tablet) {
        margin: 0 0 30px;
    }

    h5 {
        color: $primary;
        margin: 0 0 7px;

        @media (min-width: $widescreen + 1) {
            font-size: 20px;
        }
    }
}

.section-row {
    display: flex;
    flex-flow: row wrap;
    margin: -30px -45px 0;
    position: relative;
    z-index: 600;

    @media (max-width: $widescreen) {
        margin-left: -30px;
        margin-right: -30px;
    }

    @media (max-width: $desktop) {
        margin-left: -15px;
        margin-right: -15px;
    }

    @media (max-width: $tablet) {
        flex-flow: column nowrap;
    }

    &.row-reverse {
        flex-flow: row-reverse wrap;
    }

    >.section-col {

        padding: 30px 45px 0;

        @media (max-width: $widescreen) {
            padding-left: 30px;
            padding-right: 30px;
        }

        @media (max-width: $desktop) {
            padding-left: 15px;
            padding-right: 15px;
        }

        &.text-col {

            @media (min-width: $tablet + 1) {
                width: 52.2%;
            }
        }

        &.img-col {

            @media (min-width: $tablet + 1) {
                width: 47.8%;
            }
        }
    }
}

.img-shape {
    -webkit-mask-image: url(../images/img-shape.png);
    mask-image: url(../images/img-shape.png);
    -webkit-mask-size: 100%;
    mask-size: 100% 100%;
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
    margin: 0 auto;

    &.img-shape-2 {
        -webkit-mask-image: url(../images/img-shape-3.png);
        mask-image: url(../images/img-shape-3.png);
        max-width: 566px;
    }

    &.img-shape-3 {
        -webkit-mask-image: url(../images/img-shape-2.png);
        mask-image: url(../images/img-shape-2.png);
        max-width: 535px;
    }
}


.logoes-group {
    max-width: 1080px;
    margin: 0 auto;
    position: relative;
    font-size: 16px;
    color: $gray-darker;
    text-align: center;

    &.extend {
        max-width: 100%;
    }

    ul {
        display: flex;
        flex-flow: row wrap;
        margin: -40px -10px 0;
        justify-content: center;
        padding: 0;
        list-style: none;

        li {
            padding: 40px 10px 0;
            width: 16.667%;

            @media (max-width: $desktop) {
                width: 25%;
            }

            @media (max-width: $tablet) {
                width: 33.33%;
            }

            @media (max-width: $phone) {
                width: 50%;
            }

            .frame {
                color: inherit;
                display: block;

                &:hover {
                    text-decoration: none;
                    color: $primary;
                }
            }
        }

        .img-holder {
            display: flex;
            align-items: center;
            justify-content: center;
            background: #FFFFFF;
            width: 140px;
            height: 140px;
            padding: 10px;
            box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.16);
            border-radius: 8px;
            margin: 0 auto;

            @media (max-width: $desktop) {
                width: 100px;
                height: 100px;
            }

            img {
                max-height: 100%;
                width: auto;
            }
        }

        .text {
            display: block;
            font-weight: 500;
            margin: 20px 0 0;
        }
    }
}

.section-twocols {
    position: relative;

    .section-head {

        @media (min-width: $desktop + 1) {
            margin: 0 0 30px;

        }
    }



    .row-twocols {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        @media (max-width: $desktop) {
            flex-flow: column nowrap;
            border-radius: 20px;
            overflow: hidden;
        }

        &.reverse {
            @media (max-width: $desktop) {
                flex-flow: column-reverse nowrap;
            }
        }
    }

    .small-col {
        flex-shrink: 0;
        position: relative;
        z-index: 60;
        background: #F0F0F0;
        width: 590px;
        overflow: hidden;
        border-radius: 32px;

        @media (max-width: $widescreen) {
            width: 540px;
        }

        @media (max-width: $widescreen-s) {
            width: 440px;
        }

        @media (max-width: $desktop) {
            border-radius: 0;
            margin: 0;
            width: 100%;
        }

        img {
            display: block;
            width: 100%;
        }

        .col-holder {
            padding: 40px 40px 65px;

            @media (max-width: $widescreen) {
                padding: 30px;
            }

            @media (max-width: $desktop) {
                padding: 30px 20px;
            }
        }
    }

    .big-col {
        position: relative;
        margin: 59px 0 0;
        flex-grow: 1;
        z-index: 50;
        flex-basis: 0;
        overflow: hidden;
        background: #F0F0F0;
        border-radius: 32px;
        margin: 59px -200px 0 0;

        @media (max-width: $widescreen) {
            margin: 40px -150px 0 0;
        }

        @media (max-width: $desktop) {
            margin: 0;
            width: 100%;
            border-radius: 0;
            flex-grow: inherit;
            flex-basis: inherit;
        }

        .col-holder {
            padding: 40px 250px 50px 57px;

            @media (max-width: $widescreen) {
                padding: 30px 190px 30px 30px;
            }

            @media (max-width: $desktop) {
                padding: 30px 20px;
            }
        }

        .section-head p {

            @media (min-width: $desktop + 1) {

                max-width: 90%;
            }
        }
    }
}

/* Section Outsourcing */
.section-outsourcing {
    .content-boxes {
        margin: 0;
        list-style: none;
        padding: 0;

        @media (max-width: $tablet) {
            padding: 40px 0 0;
        }

        li {
            margin: 0 0 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            @media (max-width: $desktop) {
                flex-flow: column-reverse nowrap;
                text-align: center;
                margin: 0 0 45px;
            }

            &:nth-child(even) {
                .ico-bar {
                    float: right;
                    flex-flow: row-reverse nowrap;
                    margin-right: 30px;

                    @media (max-width: $tablet) {
                        margin-right: 0;
                    }

                    .text-bar {
                        margin: 0 -40px 0 0;
                        padding-left: 50px;
                        padding-right: 90px;
                    }
                }
            }

            &:last-child {
                margin-bottom: 0;
            }

            h4 {
                color: $primary;
                margin: 0 0 16px;
            }
        }
    }

    .text-box {
        width: 44%;

        @media (max-width: $desktop) {
            width: 100%;
        }
    }

    .ico-bar-holder {
        width: 537px;
        max-width: 50%;

        @media (max-width: $desktop) {
            max-width: 100%;
            margin: 0 0 40px;
        }

    }

    .ico-bar {
        display: flex;
        align-items: center;
        max-width: 435px;
        width: 100%;
        text-align: center;
        color: #343434;

        @media (max-width: $tablet) {
            max-width: none;

        }

        .ico-area {
            border-radius: 50%;
            background: #F1F4FA;
            width: 96px;
            height: 96px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            position: relative;
            z-index: 50;

            @media (max-width: $tablet) {
                margin: 0 auto;
            }

            &:before,
            &:after {
                position: absolute;
                left: -30px;
                top: -30px;
                right: -30px;
                bottom: -30px;
                z-index: -2;
                content: "";
                border-radius: 50%;
                background-color: #8EBBFF;
                border: 15px solid #F1F4FA;
                background: linear-gradient(45deg, #56a6ff 0%, #0058ff 100%);
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            }

            &:after {
                background: #F1F4FA;
                left: 0;
                z-index: -1;
                top: 0;
                right: 0;
                bottom: 0;
            }
        }

        .text-bar {
            border-radius: 60px;
            padding: 30px 50px 30px 90px;
            background: #FFFFFF;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-size: 20px;
            flex-grow: 1;
            overflow: hidden;
            margin: 0 0 0 -40px;
            line-height: 1.28;

            @media (max-width: $tablet) {
                display: none;
            }

            strong {
                display: block;
                font-weight: 600;
            }
        }
    }
}

/* Section Process */
.section-process {
    position: relative;
    overflow: hidden;

    .process-steps-holder {
        position: relative;
        overflow: hidden;
        margin: 0 -50px 0 0;
        padding: 0 50px 0 0;

        @media (max-width: 1339px) {
            padding: 0 20px 0 0;
            margin: 0 -20px 0 0;
        }

        @media (max-width: $tablet) {
            padding: 0;
            margin: 0;
        }
    }

    .process-steps {
        position: relative;
        display: flex;
        flex-flow: row wrap;
        margin: -40px -40px 0;
        overflow: hidden;

        @media (max-width: 1399px) {
            margin-left: -15px;
            margin-right: -15px;
            font-size: 16px;
        }

        &:before {
            position: absolute;
            right: 10px;
            top: 40px;
            height: 55px;
            background: $white;
            content: "";
            z-index: 5;
            width: 2px;

            @media (max-width: $desktop) {
                height: 35px;
            }

            @media (max-width: $tablet) {
                display: none;
            }
        }

        .box-step {
            flex-grow: 1;
            flex-basis: 0;
            flex-shrink: 0;
            min-width: 33.33%;
            padding: 0 40px;
            position: relative;
            margin: 40px 0 0;

            @media (max-width: 1339px) {
                padding: 0 15px;
            }

            @media (max-width: $tablet) {
                min-width: 100%;
            }


            &:after {
                position: absolute;
                left: -10px;
                right: 10px;
                top: 55px;
                background: #bfbfbf;
                height: 2px;
                content: "";

                @media (max-width: $desktop) {
                    top: 35px;
                }

                @media (max-width: $tablet) {
                    display: none;
                }
            }

            .arrow {
                position: absolute;
                left: 75%;
                top: 47px;
                width: 17px;
                height: 16px;
                z-index: 60;

                @media (max-width: $desktop) {
                    top: 26px;
                }

                @media (max-width: $tablet) {
                    display: none;
                }

            }

            &:last-child {
                &:before {
                    position: absolute;
                    right: 10px;
                    background: #bfbfbf;
                    height: 999px;
                    width: 2px;
                    bottom: 100%;
                    margin: 0 0 -55px;
                    content: "";

                    @media (max-width: $desktop) {
                        margin: 0 0 -35px;
                    }

                    @media (max-width: $tablet) {
                        display: none;
                    }
                }
            }

            &:nth-child(3) {
                .arrow {
                    top: 50%;
                    left: 100%;
                    transform: rotate(90deg);
                    margin: 50px 0 0 -18px;
                }
            }

            &:nth-child(n+4) {
                .arrow {
                    left: auto;
                    right: 43%;
                    transform: rotateY(180deg);
                }
            }

        }

        .ico-holder {
            border-radius: 55px 55px 55px 0;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            width: 105px;
            position: relative;
            height: 105px;
            z-index: 50;
            background: $secondary;
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 0 20px;

            @media (max-width: $desktop) {
                border-radius: 40px 40px 40px 0;
                width: 70px;
                height: 70px;

            }

            img {
                max-width: 65px;

                @media (max-width: $desktop) {
                    max-width: 40px;
                }
            }
        }

        h4 {
            color: $primary;
            margin: 0 0 10px;

            @media (min-width: $desktop + 1) {
                font-size: 24px;
            }
        }
    }
}

/* Section Portfolio */
.section-portfolio {
    .portfolio-main {
        position: relative;

        .portfolio-slider {
            margin: 0 -25px;
        }

        .portfolio-box {
            padding: 0 25px 15px;
            display: flex;
            width: 33.33%;

            .box-holder {
                background: $white;
                box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.2);
                border-radius: 4px;
                overflow: hidden;
            }

            .img-holder {
                position: relative;

                img {
                    display: block;
                    width: 100%;
                    height: 220px;
                    object-fit: cover;
                    object-position: 50% 50%;

                    @media (max-width: $tablet) {
                        height: auto;
                        object-fit: inherit;
                    }
                }
            }

            .text-box {
                padding: 24px;

                h4 {
                    font-size: 20px;
                    margin: 0 0 12px;

                    a {

                        &:hover,
                        &:focus {
                            text-decoration: none;
                            color: $secondary;
                        }
                    }
                }
            }
        }
    }

    .btn-block {
        position: relative;
        display: flex;
        justify-content: center;
        padding: 30px 0 0;
    }
}

/* Section Testimonials */
.section-testimonials {
    text-align: center;

    .slick-slider {
        position: static;
    }

    .testimonials-block-frame {
        position: relative;
    }

    .section-head {
        margin: 0 0 30px;
        padding: 0 140px;

        @media (max-width: $desktop) {
            padding: 0 120px;
        }

        @media (max-width: $tablet) {
            padding: 0 0 30px;
        }
    }

    .testimonials-block {
        margin: 0 -12px;
    }

    .testimonial-slide {
        padding: 15px 12px;

        .testimonial-holder {
            background: #FFFFFF;
            box-shadow: 0px 3px 14px rgba(0, 0, 0, 0.16);
            border-radius: 12px;
            padding: 40px 27px;

            @media (max-width: $widescreen) {
                padding: 15px;
            }
        }

        .img-holder {
            margin: 0 0 24px;
            min-height: 135px;
            display: flex;
            align-items: center;
            justify-content: center;

            @media (max-width: $desktop) {
                margin: 0 0 20px;
            }

            img {
                margin: 0 auto;
            }
        }

        h5 {
            margin: 0 0 12px;
            color: #343434;
        }

        h3 {
            color: $primary;

            @media (min-width: $widescreen + 1) {
                font-size: 32px;
                margin: 0 0 20px;
            }
        }
    }

    .rating {
        margin: -4px;
        padding: 0;
        list-style: none;
        justify-content: center;
        display: flex;

        li {
            padding: 4px;

            a {
                color: #F1F4FA;

                &:hover,
                &.active {
                    color: #FFBF00;
                }
            }
        }
    }

    .slick-next,
    .slick-prev {
        width: 47px;
        height: 47px;
        background: $white;
        position: absolute;
        right: 0;
        color: $primary;
        font-size: 15px;
        left: auto;
        margin: 0;
        top: -60px;
        padding: 0;

        @media (max-width: $tablet) {
            right: 50%;
            top: -50px;
            margin: 0 -30px 0 0;
            transform: translateX(50%);
        }

        &:hover {
            color: $white;
            background: $primary;
        }
    }

    .slick-prev {
        right: 60px;

        @media (max-width: $tablet) {
            right: 50%;
            margin: 0 30px 0 0;
        }
    }
}

/* Section Map */
.section-map {
    z-index: 50;

    .map-main {
        position: relative;
        width: 1300px;
        color: $gray-darker;
        margin: 0 auto;

        @media (max-width: $widescreen) {
            width: 980px
        }

        @media (max-width: $desktop) {
            width: 600px
        }

        @media (max-width: $tablet) {
            width: 320px
        }

        .map-frame img {
            display: block;
            width: 100%;
        }

        .map-pin {
            position: absolute;
            left: 0;
            top: 0;

            &.pin-1 {
                left: 7.2%;
                top: 19.5%;

                @media (max-width: $widescreen) {
                    left: 7.5%;
                    top: 22.6%;
                }

                @media (max-width: $desktop) {
                    left: 9.7%;
                    top: 20.6%;
                }

                @media (max-width: $tablet) {
                    left: 8.3%;
                    top: 11.4%;
                }

                .marker-link i {
                    color: #43BCCD;
                }

                @media (max-width: $tablet) {

                    .marker-details {
                        left: -10%;
                        transform: none;
                    }
                }
            }

            &.pin-2 {
                left: 46.7%;
                top: 34.5%;

                @media (max-width: $widescreen) {
                    left: 47%;
                    top: 36.9%;
                }

                @media (max-width: $desktop) {
                    left: 47.8%;
                    top: 35.9%;
                }

                @media (max-width: $tablet) {
                    left: 46.6%;
                    top: 27%;
                }

                .marker-link i {
                    color: #33C300;
                }
            }

            &.pin-3 {
                left: 32%;
                top: 2.8%;

                @media (max-width: $widescreen) {
                    left: 32.4%;
                    top: 5.8%;
                }

                @media (max-width: $desktop) {
                    left: 32.2%;
                    top: 3.8%;
                }

                @media (max-width: $tablet) {
                    left: 30.8%;
                    top: -5%;
                }


                .marker-link i {
                    color: #F9C80E;
                }
            }

            &.pin-4 {
                left: 17.4%;
                top: 7.4%;

                @media (max-width: $widescreen) {
                    left: 17.7%;
                    top: 10.4%;
                }

                @media (max-width: $desktop) {
                    left: 20.3%;
                    top: 8.4%;
                }

                @media (max-width: $tablet) {
                    left: 19%;
                    top: -1.6%;
                }

                .marker-link i {
                    color: #662E9B;
                }

                .text {
                    left: 20px;
                }

                @media (max-width: $tablet) {

                    .marker-details {
                        left: -10%;
                        transform: none;
                    }
                }
            }

            &.pin-5 {
                left: 34.8%;
                top: 5.2%;

                @media (max-width: $widescreen) {
                    left: 35.0%;
                    top: 7.5%;
                }

                @media (max-width: $desktop) {
                    left: 36.9%;
                    top: 5.6%;
                }

                @media (max-width: $tablet) {
                    left: 35.7%;
                    top: -3.4%;
                }

                .marker-link i {
                    color: #C15C00;
                }

                .text {
                    left: 60px;
                    top: -13px;

                    @media (max-width: $widescreen) {
                        left: 38px;
                        top: -7px;
                    }
                }
            }

            &.pin-6 {
                left: 12.2%;
                top: 12.2%;

                @media (max-width: $widescreen) {
                    left: 12.5%;
                    top: 15.0%;
                }

                @media (max-width: $desktop) {
                    left: 14.4%;
                    top: 13%;
                }

                @media (max-width: $tablet) {
                    left: 13.2%;
                    top: 4%;
                }

                .marker-link i {
                    color: #20007F;
                }

                @media (max-width: $tablet) {

                    .marker-details {
                        left: -10%;
                        transform: none;
                    }
                }
            }

            &.pin-7 {
                left: 51.6%;
                top: 31.2%;

                @media (max-width: $widescreen) {
                    left: 51.9%;
                    top: 34.3%;
                }

                @media (max-width: $desktop) {
                    left: 53.3%;
                    top: 33.1%;
                }

                @media (max-width: $tablet) {
                    left: 52.3%;
                    top: 24.5%;
                }

                .marker-link i {
                    color: #FF5C00;
                }
            }

            &.pin-8 {
                left: 35.55%;
                top: 22%;

                @media (max-width: $widescreen) {
                    left: 35.8%;
                    top: 25.2%;
                }

                @media (max-width: $desktop) {
                    left: 36.7%;
                    top: 23%;
                }

                @media (max-width: $tablet) {
                    left: 33.7%;
                    top: 19%;
                }

                .marker-link i {
                    color: #F40808;
                }
            }

            &.pin-9 {
                left: 41.3%;
                top: 38.1%;

                @media (max-width: $widescreen) {
                    left: 41.5%;
                    top: 41.2%;

                }

                @media (max-width: $desktop) {
                    left: 42.1%;
                    top: 39.3%;
                }

                @media (max-width: $tablet) {
                    left: 40.8%;
                    top: 30.4%;
                }

                .marker-link i {
                    color: #0085FF;
                }
            }

            &.pinActive {
                z-index: 50;

                .marker-details {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }

        .marker-link {
            display: block;
            color: inherit;
            text-align: center;

            &:hover {
                text-decoration: none;

                .text {
                    color: $primary;
                }
            }

            i {
                font-size: 57px;
                line-height: 1;
                display: block;
                margin: 0 0 25px;

                @media (max-width: $widescreen) {
                    font-size: 30px;
                    margin: 0 0 22px;
                }

                @media (max-width: $desktop) {
                    font-size: 24px;
                    margin: 0 0 12px;
                }
            }

            .text {
                display: block;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                max-width: 150px;
                position: relative;
                font: 600 20px/1.2 "Poppins", Arial;

                @media (max-width: $widescreen) {
                    font-size: 14px;
                    max-width: 100px;

                }

                @media (max-width: $desktop) {
                    display: none;
                }
            }
        }

        .marker-details {
            position: absolute;
            left: 50%;
            top: 100%;
            width: 400px;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            display: flex;
            align-items: center;
            padding: 10px;
            transform: translateX(-50%);
            border-radius: 8px;
            margin: 10px 0 0;
            opacity: 0;
            visibility: hidden;
            transition: all .3s ease-in-out;
            background: #fff;

            @media (max-width: $widescreen-s) {
                display: block;
                padding: 0;
                overflow: hidden;
                width: 240px
            }

            @media (max-width: $desktop) {
                width: 200px;
                border-radius: 4px;
                margin: 0;
            }

            @media (max-width: $tablet) {
                width: 180px;
            }

            .img {
                width: 154px;
                flex-shrink: 0;
                margin: 0 10px 0 0;

                @media (max-width: $widescreen-s) {
                    width: 100%;
                    margin: 0;
                }
            }

            .text-box {
                flex-grow: 1;
                overflow: hidden;

                @media (max-width: $widescreen-s) {
                    padding: 10px;
                }
            }

            h4 {
                font-size: 18px;
                color: $gray-darker;
                line-height: 1.2;
                margin: 0 0 12px;

                @media (max-width: $desktop) {
                    font-size: 15px;
                }
            }

            .contact-list {
                margin: 0;
                padding: 0;
                font-size: 14px;
                list-style: none;
                color: #6D6D6D;

                @media (max-width: $desktop) {
                    font-size: 12px;
                }

                li {
                    margin: 0 0 10px;
                    display: flex;
                    align-items: flex-start;

                    &:last-child {
                        margin: 0;
                    }

                    i {
                        color: $primary;
                        flex-shrink: 0;
                        width: 15px;
                        text-align: center;
                        margin: 3px 7px 0 0;
                    }

                    a {
                        display: flex;
                        width: 100%;
                        align-items: flex-start;
                        color: inherit;
                        word-break: break-all;

                        &:hover {
                            text-decoration: none;
                            color: $primary;
                        }
                    }
                }
            }
        }
    }
}

/* Portfolio Page */
.page-portfolio {
    height: 100%;

    body {
        height: 100%;
    }

    #wrapper {
        height: 100%;

        @media (max-width: $tablet) {
            height: auto;
        }
    }

    .slider-scrollbar {
        display: none;
    }
}


.portfolio-section {
    position: relative;
    height: calc(100vh - 100px);
    justify-content: flex-end;
    display: flex;
    z-index: 500;
    overflow: auto;
    margin-top: 100px;

    @media (max-width: $tablet) {
        height: auto;
        overflow: hidden;
    }

    .portfolio-fixed {
        position: fixed;
        left: 0;
        top: 0;
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        text-align: center;
        background: $white;

        @media (max-width: $tablet) {
            display: none;
        }

        &:after {
            position: absolute;
            z-index: 500;
            left: 0;
            cursor: pointer;
            top: 0;
            right: 0;
            bottom: 0;
            content: "";
        }

        &:before {
            background: rgba(0, 0, 0, .1);
            position: absolute;
            z-index: 500;
            left: 100%;
            background: linear-gradient(106.11deg, #0F1013 1.09%, #181B8E 100%);
            cursor: pointer;
            top: 0;
            width: 100%;
            bottom: 0;
            content: "";
        }

        .fixed-holder {
            width: 100%;
            padding: 150px 60px;

            @media (max-width: $widescreen) {
                padding: 100px 60px;
            }

            @media (max-width: $desktop) {
                padding: 100px 20px;
            }
        }
    }

    .portfolio-numbers {
        max-width: 80%;
        margin: 0 auto;
        padding: 0 0 80px;

        @media (max-width: $widescreen) {
            max-width: 100%;
        }

        h2 {
            font-family: 'DM Serif Display', "Poppins";
            font-weight: 400;
            color: $secondary;
            text-transform: none;
            letter-spacing: .06em;

            @media (max-width: $widescreen-xl) {
                font-size: 34px;
            }
        }

        .swiper-slide {
            opacity: 1 !important;
            transition: none !important;
            visibility: hidden;

            &.swiper-slide-active {
                visibility: visible;
            }
        }

        .number-slide-frame {
            background: $white;
        }

        h2 {
            opacity: 0;
            transition: opacity .3s cubic-bezier(0.77, 0, 0.175, 1) 0s;
        }

        .swiper-slide-active {

            h2 {
                opacity: 1;
            }
        }
    }

    .number {
        font-size: 220px;
        line-height: 1;
        color: $primary;
        font-weight: 700;
        margin: 0 0 20px;
        display: flex;
        overflow: hidden;
        justify-content: center;

        @media (max-width: $widescreen-xl) {
            font-size: 170px;
        }

        @media (max-width: $desktop) {
            font-size: 120px;
        }

        @media (max-width: $tablet) {
            font-size: 80px;
        }

        .figure {
            transform: translateY(100%);
            transition: all .4s ease-in-out 0s;

            &:nth-child(2) {
                transition-delay: .2s;
            }
        }
    }

    .swiper-slide-active .number .figure {
        transform: translateY(0);
    }

    .portfolio-content {
        background: linear-gradient(106.11deg, #0F1013 1.09%, #181B8E 100%);
        width: 100%;

    }

    .portfolio-block-main {
        position: relative;
        display: flex;
        justify-content: flex-end;

        @media (max-width: $tablet) {
            display: block;

        }
    }

    .portfolio-block {
        padding: 150px 100px;
        position: relative;
        width: 50%;
        position: relative;
        color: #DEDEDE;

        @media (max-width: $widescreen) {
            padding: 120px 50px;
        }

        @media (max-width: $desktop) {
            padding: 100px 20px;
        }

        @media (max-width: $tablet) {
            width: 100%;
            padding: 80px 20px 0
        }

        .section-head {
            margin: 0 0 35px;

            @media (max-width: $widescreen) {
                margin: 0 0 20px;
            }
        }

        h2 {
            color: $white;
        }

        .img-corner-holder {
            text-align: left;
        }
    }

    .content-slide:first-child .portfolio-block {
        padding-top: 50px;
    }

    .text-box {
        padding: 15px 0 15px 30px;
        border-left: 1px solid $white;
        font-size: 20px;
        line-height: 1.5;
        max-width: 550px;

        @media (max-width: $widescreen) {
            padding-left: 20px;
            padding: 10px 0 10px 20px;
            font-size: 18px;
        }

        @media (max-width: $desktop) {
            font-size: 16px;
        }

        p {
            font-weight: 300;
        }
    }

    .swiper-wrapper {
        @media (max-width: $tablet) {
            height: auto !important;
            transform: none !important;
        }
    }
}

.portfolio-range {
    position: fixed;
    left: 0;
    width: 50%;
    bottom: 60px;
    padding: 0 60px;
    display: flex;
    z-index: 1000;
    align-items: center;

    @media (max-width: $widescreen) {
        bottom: 40px;
        padding: 0 40px;
    }

    @media (max-width: $desktop) {
        padding: 0 20px;
    }

    @media (max-width: $tablet) {
        display: none;
    }

    .number-text {
        font-size: 40px;
        line-height: 1.28;
        flex-shrink: 0;
        color: $primary;
        text-align: left;
        width: 85px;
        padding: 0 5px 0 0;

        @media (max-width: $widescreen) {
            font-size: 30px;
            width: 50px;
        }

        &:last-child {
            text-align: right;
            padding: 0 0 0 10px;
        }
    }

    .range-bar {
        position: relative;
        flex-grow: 1;
        height: 46px;
        overflow: hidden;


        &:before {
            position: absolute;
            left: 0;
            right: 0;
            height: 2px;
            top: 50%;
            content: "";
            transform: translateY(-50%);
            border-bottom: 1px dashed $black;
        }
    }

    .swiper-scrollbar-drag {
        position: absolute;
        border: 1px solid #6D6D6D;
        border-radius: 50%;
        background: $white;
        width: 46px;
        cursor: move;
        height: 46px;
        left: 0;
        top: 50%;
        margin: -23px 0 0;

        &:before {
            position: absolute;
            left: 50%;
            top: 50%;
            height: 19px;
            width: 8px;
            content: "";
            background: url(../images/ico-scroll.svg) no-repeat;
            transform: translate(-5px, -50%);
        }

        &:after {
            position: absolute;
            right: 100%;
            width: 999px;
            height: 1px;
            background: #6D6D6D;
            top: 50%;
            content: "";
        }
    }

}


/* Two Columns Layout */
#content-twocols {
    display: flex;
    padding: 150px 0;

    @media (max-width: $widescreen) {
        padding: 100px 0;
    }

    @media (max-width: $desktop) {
        padding: 75px 0;
        display: block;
    }

    @media (max-width: $tablet) {
        padding: 50px 0;
    }

    .breadcrumb-holder+& {
        padding-top: 0 !important;
    }

    >.container {
        display: flex;
        width: 100%;

        @media (max-width: $desktop) {
            display: block;
        }
    }

    #aside {
        flex-shrink: 0;
        width: 350px;
        margin: 0 0 0 40px;

        .page-service & {
            @media (min-width: $widescreen + 1) {
                width: 420px;
                margin: 0 0 0 55px;
            }
        }

        @media (max-width: $widescreen-s) {
            margin: 0 0 0 20px;
            width: 300px;
        }

        @media (max-width: $desktop) {
            margin: 30px 0 0;
            width: 100%;
        }
    }

    #content {
        flex-grow: 1;

        @media (max-width: $desktop) {
            width: 100%;
        }
    }
}

/* Page Blog */

.post {
    position: relative;
    font-size: 20px;
    line-height: 1.5;
    margin: 0 0 120px;

    @media (max-width: $widescreen) {
        margin: 0 0 60px;
        font-size: 18px;
    }

    @media (max-width: $desktop) {
        margin: 0 0 30px;
        font-size: 16px;
    }

    &:last-child {
        margin-bottom: 0;
    }


    .img-corner-holder.reverse {
        position: relative;
        margin: 0 0 98px;

        @media (max-width: $widescreen) {
            margin: 0 0 70px;
        }

        @media (max-width: $desktop) {
            margin: 0 0 50px;
        }

        .img-corner-frame {
            padding: 0 28px 0 0;
            margin: 0 41px 0 0;
            display: block;

            @media (max-width: $widescreen) {
                margin: 0 20px 0 0;
                padding: 0 20px 0 0;
            }

            &:before {
                display: none;
            }

            .img-frame {
                &:before {
                    border-color: $secondary;
                    left: 28px;
                    top: 28px;
                    right: -28px;
                    bottom: -28px;

                    @media (max-width: $widescreen) {
                        left: 20px;
                        top: 20px;
                        right: -20px;
                        bottom: -20px;
                    }

                }

                &:after {
                    background: $primary;
                    width: 46px;
                    height: 46px;
                    margin: 4px 0 0 5px;

                    @media (max-width: $widescreen) {
                        width: 30px;
                        height: 30px;

                    }
                }
            }
        }
    }

    p {
        font-weight: 300;
    }

    h2 {
        color: $secondary;
        text-transform: none;
        font-weight: 700;
        font-size: 40px;

        @media (max-width: $widescreen) {
            font-size: 30px;
        }

        @media (max-width: $desktop) {
            font-size: 25px;
        }

        a:hover {
            text-decoration: none;
            color: $primary;
        }
    }

    .btn-block {
        padding: 40px 0 0 10px;

        @media (max-width: $widescreen) {
            padding-top: 30px;
        }
    }

    .date {
        position: absolute;
        left: 0;
        top: 0;
        overflow: hidden;
        border-radius: 8px 3px 0 3px;
        width: 122px;
        height: 122px;
        text-align: left;
        z-index: 50;
        padding: 13px;
        text-transform: uppercase;
        font: 32px/1 "Poppins", Arial;
        color: $white;

        .date-holder {
            width: 44%;
            display: block;
            text-align: center;
        }

        span {
            display: block;
            text-align: center;
            font-size: 16px;
        }

        &:before {
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 122px 122px 0 0;
            border-color: $secondary transparent transparent;
            position: absolute;
            left: 0;
            z-index: -1;
            top: 0;
            content: "";
        }
    }

    &.post-details {
        font-size: 20px;
        line-height: 1.37;
        margin: 0 0 60px;

        @media (max-width: $widescreen) {
            font-size: 18px;
        }

        @media (max-width: $desktop) {
            font-size: 16px;
            margin: 0 0 40px;
        }

        p {
            font-weight: 400;
        }
    }

    .meta {
        margin: -20px -10px 0;
        padding: 48px 69px 0 0;
        list-style: none;
        position: relative;
        z-index: 50;
        display: flex;
        flex-flow: row wrap;
        font-size: 18px;
        justify-content: flex-end;

        @media (max-width: $widescreen) {
            padding: 35px 40px 0 0;
            font-size: 16px;
        }

        li {
            padding: 20px 10px 0;

            a {
                color: inherit;
            }
        }
    }

    .quote-text {
        background: #F1F4FA;
        border-radius: 8px;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        color: $primary;
        padding: 40px 20px;
        margin: 60px 0;

        .quote-holder {
            max-width: 550px;
            margin: 0 auto;
            display: flex;

            &:before {
                content: "";
                color: $secondary;
                margin: -22px 6px 0 0;
                font-size: 64px;
                font-weight: 700;
                flex-shrink: 0;
            }
        }

        blockquote {
            font: italic 600 26px/1.28 "Poppins", Arial;
            border: 0;
            margin: 0;
            padding: 0;

            q {
                quotes: none;
            }
        }
    }

    .text-block {
        padding: 60px 0;

        &:last-child {
            padding-bottom: 0;
        }

        &:first-child {
            padding-top: 0;
        }

        h2 {

            @media (min-width: $widescreen + 1) {
                margin: 0 0 40px;
            }
        }
    }

    ul:not([class]) {
        >li {
            margin: 0 0 20px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

/* Sidebar Widgets */
.widget {
    padding: 60px 0 40px;
    border-bottom: 6px solid $secondary;
    font-family: "Poppins", Arial;
    position: relative;

    @media (max-width: $widescreen) {
        padding: 30px 0;
    }

    &:first-child {
        padding-top: 0;
    }

    &:last-child {
        padding-bottom: 0;
        border: 0;
    }

    h4 {
        color: $secondary;
        margin: 0 0 40px;
        font-weight: 700;

        @media (max-width: $widescreen) {
            margin: 0 0 25px;
        }
    }
}

.widget-search {

    .widget-holder {
        background: #FFFFFF;
        border: 1px solid rgba(#707070, .2);
        box-shadow: 0px 2px 4px 0 rgba(0, 0, 0, 0.25);
        border-radius: 4px;
        overflow: hidden;
    }

    form {
        display: flex;

        .btn-primary {
            flex-shrink: 0;
            background: $primary;
            color: $white;
            width: 62px;
            border: 0;
            padding: 0;
            height: 50px;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px;
            min-width: inherit;
            border-radius: 0;
        }

        .input-holder {
            flex-grow: 1;
            overflow: hidden;
            margin: 0;
        }

        .form-control {
            border: 0;
            box-shadow: none;
            height: 50px;
            padding: 16px;
            font-weight: 400;
            font-size: 14px;
            line-height: 18px;
            display: block;
            width: 100%;
            background: $white;
        }
    }
}

.widget-posts {
    .post-small {
        display: flex;
        align-items: center;
        margin: 0 0 40px;

        @media (max-width: $widescreen) {
            margin: 0 0 25px;
        }

        &:last-child {
            margin: 0;
        }

        .img-holder {
            flex-shrink: 0;
            width: 90px;
            margin: 0 19px 0 0;

            img {
                display: block;
                width: 100%;
                height: auto;
                border-radius: 4px;
            }
        }

        .text-box {
            flex-grow: 1;
            overflow: hidden;
        }

        .date {
            display: block;
            text-transform: uppercase;
            margin: 0 0 6px;
            font-size: 16px;
        }

        h5 {
            color: $secondary;

            a:hover {
                text-decoration: none;
                color: $primary;
            }
        }
    }
}

.widget-categories {
    ul {
        margin: 0;
        font-size: 20px;
        font-weight: 500;
        padding: 0;
        list-style: none;

        @media (max-width: $widescreen) {
            font-size: 16px;
        }

        li {
            margin: 0 0 20px;

            @media (max-width: $widescreen) {
                margin: 0 0 15px;
            }

            a {
                color: inherit;

                &:hover {
                    color: $primary;
                    text-decoration: none;
                }
            }

            &:last-child {
                margin: 0;
            }
        }
    }
}

.widget-tags {
    ul {
        display: flex;
        flex-flow: row wrap;
        margin: -20px -10px 0;
        padding: 0;
        list-style: none;
        font-size: 14px;
        font-weight: 500;

        li {
            padding: 20px 10px 0;

            a {
                display: block;
                background: #EAEAEA;
                padding: 14px 15px;
                border-radius: 2px;
                color: inherit;
                text-align: center;
                min-width: 90px;

