}

.widget-content {
    border-top: 1px solid #C3C3C3;
    padding: 60px 0;

    @media (max-width: $widescreen) {
        padding: 30px 0;
    }

    &:last-child {
        padding-bottom: 0;
    }

    .widget-holder {
        margin: 0 0 40px;

        @media (max-width: $widescreen) {
            margin: 0 0 25px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    h4 {
        color: $secondary;
    }

    .popular-tags {
        margin: -16px -8px 40px;
        display: flex;
        flex-flow: row wrap;
        padding: 0;
        list-style: none;
        font: 500 14px/18px "Poppins", Arial;

        @media (max-width: $widescreen) {
            margin: 0 0 25px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        >li {
            padding: 16px 8px 0;

            >a {
                color: #343434;
                padding: 10px;
                min-width: 123px;
