                text-align: center;

                &:hover {
                    text-decoration: none;
                    background: $secondary;
                    color: $white;
                }
            }
        }
    }

    .share-holder {
        margin: -20px -10px 0;
        display: flex;
        flex-flow: row wrap;
        padding: 0;
        list-style: none;
        align-items: center;

        li {
            padding: 20px 10px 0;

            a {
                display: block;
            }
        }
    }
}

/* Comments List */
ol.comments-list {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 16px;
    border-bottom: 1px solid #C3C3C3;

    >li {
        border-top: 1px solid #C3C3C3;

        &:first-child {
            border: 0;
        }

        .comment-holder {
            display: flex;
            align-items: flex-start;

            padding: 30px 0;
        }

        .avatar {
            flex-shrink: 0;
            width: 60px;
            height: 60px;
            border-radius: 50%;
            background: #fff url(../images/avatar.png) no-repeat;
            margin: 0 15px 0 0;
        }

        .text-block {
            flex-grow: 1;
            overflow: hidden;
        }

        >ol {
            margin: 0;
            padding: 0;
            list-style: none;

