                padding-left: 80px;
                border-top: 1px solid #C3C3C3;
            }
        }

        ul.comment-meta {
            margin: -20px -10px 10px;
            display: flex;
            font-family: "Poppins", Arial;
            flex-flow: row wrap;
            padding: 0;
            list-style: none;

            &:last-child {
                margin-bottom: 0;
            }

