                padding: 20px 10px 0;
                border: 0;

                a {
                    color: #343434;
                    font-weight: 600;

                    &.reply {
                        color: #FF3B33;
                        font-weight: 500;
                        text-transform: uppercase;
                    }
                }
            }
        }

