            margin: 0 0 10px;
        }
    }
}

/* Section About Us */
.section-about {

    &.reverse .about-content {
        flex-flow: row-reverse nowrap;
    }

    .about-content {
        display: flex;
        align-items: center;
        margin: -30px -30px 0;

        @media (max-width: $widescreen) {
            margin-left: -15px;
            margin-right: -15px;
        }

        @media (max-width: $desktop) {
            display: block;
        }



        >* {
            padding: 30px 30px 0;

            @media (max-width: $widescreen) {
                padding-left: 15px;
                padding-right: 15px;
            }
        }
    }

    .img-holder {
        flex-shrink: 0;
        max-width: 50%;

        @media (max-width: $desktop) {
            max-width: 100%;
        }

        img {
            margin: 0 auto;
        }
    }

    .text-block {
        flex-grow: 1;
        overflow: hidden;
    }

    .section-head {
        margin: 0 0 40px;

        @media (max-width: $widescreen) {
            margin: 0 0 30px;
        }
    }

    .services-list {
        margin: -20px -10px 0;
        display: flex;
        padding: 0;
        list-style: none;
        flex-flow: row wrap;

        @media (max-width: $tablet) {
            display: block;
        }

        li {
            width: 50%;
            padding: 20px 10px 0;

            @media (max-width: $tablet) {
                width: 100%;
            }
        }

        .ico {
            background: #E9F0FF;
            width: 105px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 0 16px;
            height: 105px;
            border-radius: 0 50% 50% 50%;
        }
    }

    &.about-modified {
        .services-list {
            display: block;

            @media (min-width: $tablet + 1) {
                max-width: 80%;
                padding: 16px 0 0;
                margin-top: -40px;

                li {
                    width: 100%;
                    display: flex;
                    align-items: center;
                    padding-top: 40px;
                }

                .ico {
                    flex-shrink: 0;
                    margin: 0 22px 0 0;
                }

                .text-box {
                    flex-grow: 1;
                    overflow: hidden;
                }
            }

            .ico {
                border-radius: 50% 0 50% 50%;
            }
        }
    }

    &:not(.about-modified) {
