}

/* Section Services */
.section-services {
    text-align: center;
    z-index: 50;

    .section-frame {
        @media (min-width: $desktop + 1) {

            padding-bottom: 0 !important;
        }
    }

    .services-block {
        display: flex;
        flex-flow: row wrap;
        margin: -24px -12px -120px;

        @media (max-width: $widescreen) {
            margin-bottom: -80px;
        }

        @media (max-width: $desktop) {
            margin-bottom: 0;
        }

        .box-service {
            padding: 24px 12px 0;
            width: 33.33%;
            display: flex;

            @media (max-width: $tablet) {
                width: 100%;
            }

