                &:hover {
                    text-decoration: none;
                    color: $primary;
                }
            }

            .box-holder {
                width: 100%;
                background: $white;
                box-shadow: 0px 3px 14px rgba(0, 0, 0, 0.16);
                border-radius: 12px;
