                    max-height: 100%;
                    width: auto;
                    max-width: none;
                }
            }
        }
    }

    +.section .section-frame {

        @media (min-width: $desktop + 1) {
            padding-top: 200px;
        }

        @media (min-width: $widescreen + 1) {
            padding-top: 240px;
        }
    }
}

/* FAQ Accordion */

.accordion-faq {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
        border-bottom: 1px solid #C3C3C3;

        &.accActive h5 a .ico:after {
            opacity: 0;
        }

        &.accActive h5 a {
            color: $primary;
        }
    }

    .faq-content {
        display: none;

        .faq-holder {
            padding: 0 0 20px;
        }
    }

    h5 {
        a {
            display: flex;
            align-items: flex-start;
            padding: 20px 0;

            &:hover {
                text-decoration: none;
                color: $primary;
            }

            .ico {
                background: $primary;
                width: 30px;
                height: 30px;
                border-radius: 4px;
                position: relative;
                margin: 0 15px 0 0;

                &:after,
                &:before {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    width: 18px;
                    background: $white;
                    height: 2px;
                    content: "";
                }

                &:after {
                    height: 18px;
                    width: 2px;
                }
            }

            span {
                flex-grow: 1;
                overflow: hidden;
            }
        }
    }
}

/* Section Why */
.section-why {
    text-align: center;

    .container {
        max-width: 1170px;
    }

    .features-list {
        margin: -80px -35px 0;
        display: flex;
        flex-flow: row wrap;
        padding: 0;
        list-style: none;

        @media (max-width: $widescreen) {
            margin: -60px -25px 0;
        }

        @media (max-width: $desktop) {
            margin: -30px -15px 0;
        }

        li {
            width: 33.33%;
            padding: 80px 35px 0;

            @media (max-width: $widescreen) {
                padding: 60px 25px 0;
            }

            @media (max-width: $desktop) {
                padding: 30px 15px 0;
            }

            @media (max-width: $tablet) {
                width: 100%;
            }
        }

        .ico-holder {
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 auto 40px;

            img {
                max-width: none;
                max-height: 100%;
                width: auto;
            }
        }
    }
}

/* Section Procedure  */
.section-procedure {
    .procedure-steps {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        text-align: center;
        justify-content: center;
        margin: 0 0 -60px;

        @media (max-width: $tablet) {
            margin: 0 0 -30px;
        }

        .step {
            width: 33.33%;
            display: flex;
            margin: 0 0 60px;
            position: relative;
            font-size: 16px;
            line-height: 1.375;
            justify-content: center;

            @media (max-width: $desktop) {
                font-size: 14px;
            }

            @media (max-width: $tablet) {
                width: 100%;
                margin: 0 0 30px;
            }

            &:before {
                position: absolute;
                left: 0;
                right: 0;
                top: 52px;
                background: #F8F9FC;
                content: "";
                bottom: 0;
            }

            .step-holder {
                position: relative;
                padding: 0 15px 40px;
                width: 100%;
                max-width: 275px;

            }

            .ico {
                background: $primary;
                width: 105px;
                height: 105px;
                border-radius: 50% 50% 0 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto 20px;

            }

            h4 {
                color: $primary;
                margin: 0 0 10px;
            }
        }
    }
}

/* Section Projects */
.section-projects {
    position: relative;
    color: $white;
    z-index: 50;

    &.add {
        padding: 150px 0 0;

        @media (max-width: $widescreen) {
            padding-top: 120px;
        }

        @media (max-width: $desktop) {
            padding-top: 80px;
        }

        @media (max-width: $tablet) {
            padding-top: 40px;
        }


        .section-frame {
            padding-bottom: 0 !important;
        }
    }

    .section-head {
        padding-right: 120px;

        @media (max-width: $tablet) {
            padding: 0 0 50px;
        }
    }

    .section-frame {

        &:before,
        &:after {
            position: absolute;
            left: 0;
            top: 0;
            width: 58%;
            background: $primary;
            z-index: -1;
            content: "";
            height: 520px;

            @media (max-width: $widescreen) {
                height: 450px;
            }

            @media (max-width: $desktop) {
                height: 350px;
            }

            @media (max-width: $tablet) {
                width: 100%;
            }
        }

        &:after {
            background: #F1F4FA url(../images/bg-projects-dots.png) no-repeat 100% 0;
            left: 58%;
            width: 42%;

            @media (max-width: $tablet) {
                display: none;
            }
        }
    }

    h2,
    h3 {
        color: inherit;
    }

    .projects-wrap {
        position: relative;
    }

    .projects-main {
        margin: 0 -15px;
        max-width: 86.1%;
        position: static;

        @media (max-width: $desktop) {
            max-width: 94%;
        }

        .project-slides {
            position: static !important;

        }

        .slick-list {
            margin: 0 -999px 0 0;
            padding: 0 999px 0 0;
        }
    }

    .project-slide {
        padding: 15px;

        .project-frame {
            display: block;
            position: relative;
            overflow: hidden;
            color: inherit;
            border-radius: 4px;
            box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.2);

            &:hover {
                .img-holder:after {
                    opacity: 1;
                    visibility: visible;
                }

                .text-box {
                    transform: translateX(0);
                }
            }
        }

        .img-holder {
            position: relative;

            &:after {
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                opacity: 0;
                visibility: hidden;
                transition: all .3s ease-in-out;
                content: "";
                background: rgba($primary, .3);
            }

            img {
                width: 100%;
            }
        }

        .text-box {
            position: absolute;
            left: 0;
            border-radius: 0 8px 8px 0;
            padding: 25px 16px;
            bottom: 60px;
            width: 280px;
            transform: translateX(-100%);
            background: $white;
            transition: transform .3s ease-in-out;

            @media (max-width: $desktop) {
                bottom: 20px;
                width: 240px;
                padding: 15px 12px;
            }
        }

        h6 {
            color: $primary;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        h3 {
            color: $secondary;
            font-weight: 700;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    .slick-next,
    .slick-prev {
        width: 47px;
        height: 47px;
        background: $white;
        position: absolute;
        right: 0;
        color: $primary;
        font-size: 15px;
        left: auto;
        margin: 0;
        top: -60px;
        padding: 0;

        @media (max-width: $tablet) {
            right: 50%;
            top: -50px;
            margin: 0 -30px 0 0;
            transform: translateX(50%);
        }

        &:hover {
            color: $white;
            background: $primary;
        }
    }

    .slick-prev {
        right: 60px;

        @media (max-width: $tablet) {
            right: 50%;
            margin: 0 30px 0 0;
        }
    }
}

/* Widget Reply  */


.widget-reply {
