            font-weight: 400;
            border-radius: 5px;
        }

        textarea.form-control {
            height: 100px;
        }

        .btn-block {
            padding: 0;

            .btn {
                flex-grow: 1;
            }
        }
    }
}

/* Section Textblock  */
.section-textblock {

    &:not(.section-textblock-add) {
        font-size: 24px;
        line-height: calc(33/24);

        @media (max-width: $widescreen) {
            font-size: 20px;
        }

        @media (max-width: $desktop) {
            font-size: 18px;
        }
    }

    .textblock-frame {
        display: flex;
        margin: -20px -52px 0;
        justify-content: space-between;
        align-items: center;

        @media (max-width: $widescreen) {
            margin-left: -30px;
            margin-right: -30px;
        }

        @media (max-width: $widescreen-s) {
            margin-left: -15px;
            margin-right: -15px;
        }

        @media (max-width: $desktop) {
            display: block;
            text-align: center;
        }

        >* {
            padding: 20px 52px 0;

            @media (max-width: $widescreen) {
                padding-left: 30px;
                padding-right: 30px;
            }

            @media (max-width: $widescreen-s) {
                padding-left: 15px;
                padding-right: 15px;
            }
        }

        h2 {
            letter-spacing: .07em;
        }

        .textblock-head {
            flex-shrink: 0;
            width: 40%;

            @media (max-width: $desktop) {
                width: 100%;
            }
        }

        .textblock-holder {
            flex-grow: 1;
            overflow: hidden;
        }
    }
}

/* Section InfoText */
.section-infotext {
    text-align: center;

    .container {
        max-width: 1140px;
    }

    .sign-line {
        margin-left: auto;
        margin-right: auto;
    }
}

/* Portfolio Details */
.portfolio-details {

    .section-head {
        text-align: center;
        max-width: 1070px;
        margin: 0 auto;

        @media (min-width: $widescreen + 1) {
            font-size: 20px;
        }
    }

    .item-detail {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 160px 0 0;

        @media (max-width: $widescreen) {
            padding: 120px 0 0;
        }

        @media (max-width: $desktop) {
            padding: 80px 0 0;
            display: block;
        }

        &:nth-child(odd) {
            flex-flow: row-reverse nowrap;

            .img-wrap {
                &:after {
                    right: -70px;
                    background: $secondary;
                    left: auto;

                    @media (max-width: $widescreen) {
                        left: auto;
                        right: -55px;
                    }

                    @media (max-width: $desktop) {
                        left: auto;
                        right: -30px;
                    }
                }

                &:before {
                    left: auto;
                    right: 100%;
                    background-image: url(../images/arrow-big-gray.svg);
                    transform: translateX(50%);

                }
            }
        }
    }

    .img-wrap {
        flex-shrink: 0;
        width: 365px;
        margin: 0 140px;
        position: relative;
        z-index: 50;

        @media (max-width: $widescreen) {
            margin: 0 120px;
        }

        @media (max-width: $widescreen-s) {
            margin: 0 80px;
        }

        @media (max-width: $desktop) {
            width: 280px;
            margin: 0 auto;
        }

        &:after {
            position: absolute;
            left: -70px;
            top: -70px;
            width: 230px;
            background: $primary;
            z-index: -1;
            height: 230px;
            border-radius: 50%;
            content: "";

            @media (max-width: $widescreen) {
                width: 180px;
                height: 180px;
                left: -55px;
                top: -55px;
            }

            @media (max-width: $desktop) {
                width: 100px;
                height: 100px;
                left: -30px;
                top: -30px;
            }
        }

        &:before {
            position: absolute;
            left: 100%;
            background: url(../images/arrow-big-blue.svg) no-repeat 0 0 / 100% auto;
            content: "";
            transform: translateX(-50%);
            bottom: 0;
            width: 147px;
            height: 167px;

            @media (max-width: $tablet) {
                width: 50px;
                height: 70px;
            }
        }

        .img-frame {
            position: relative;
            background: $black;
            border-radius: 30px;
            z-index: 50;
            padding: 10px;
            width: 100%;
            margin: 0 auto;
            box-shadow: -40px 33px 40px rgba(0, 0, 0, 0.129);

            img {
                display: block;
                width: 100%;
                border-radius: 20px;
            }
        }
    }

    .text-box {
        width: 45%;

        @media (max-width: $desktop) {
            width: 100%;
            padding: 40px 0 0;
        }
    }
}


/* Intro Landing Pages */
.section-intro-landing {
    padding: 130px 0 40px;
    display: flex;
    min-height: 600px;
    position: relative;
    background: linear-gradient(180deg, #FFFFFF 0%, #D1E8FF 102.41%);
    align-items: center;
    justify-content: space-between;
    font-size: 24px;
    line-height: calc(33/24);

    @media (max-width: $widescreen) {
        font-size: 20px;
    }

    @media (max-width: $desktop) {
        font-size: 16px;
        min-height: inherit;
        padding-top: 95px;
    }

    @media (max-width: $tablet) {
        text-align: center;
    }

    >.container {
        width: 100%;
        justify-content: space-between;
        display: flex;
        position: relative;
        z-index: 500;
        align-items: center;

        @media (max-width: $tablet) {
            display: block;
        }
    }

    &:after,
    &:before {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        z-index: 100;
        height: 100%;
        pointer-events: none;
        content: "";
        background: url(../images/img-pattern.svg) no-repeat 0 0;
    }

    &:after {
        background: url(../images/img-pattern-2.svg) no-repeat 100% 100%;

    }

    .text-box {
        flex-grow: 1;
        overflow: hidden;
        max-width: 680px;

        @media (max-width: $tablet) {
            max-width: 100%;
            padding: 0 0 30px;
        }

        h2 {
            .border-line {
                @media (max-width: $tablet) {

                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
    }

    .btn-block {
        @media (max-width: $tablet) {
            justify-content: center;
            padding: 0;
        }
    }

    .img-area {
        flex-shrink: 0;
        width: 431px;
        margin: 0 0 0 20px;

        @media (max-width: $widescreen) {
            width: 400px;
        }

        @media (max-width: $desktop) {
            width: 340px;
        }

        @media (max-width: $tablet) {
            width: auto;
            max-width: 600px;
            margin: 0 auto;
        }
    }

    h2 {
        @media (min-width: $widescreen + 1) {
            font-size: 40px;
        }
    }

    .btn-secondary-fancy {
        color: $secondary;
    }

    &.modified {
        text-align: center;

        &:before {
            background-image: url(../images/img-pattern-3.svg);
        }

        &:after {
            background-image: url(../images/img-pattern-4.svg);
        }

        h1 {
            font-family: "Poppins", Arial, sans-serif;
            color: $secondary;
            text-transform: capitalize;
            padding: 0;
            margin: 0 0 12px;
            font-weight: 700;

            @media (min-width: $widescreen + 1) {
                font-size: 72px;
            }

            &:after {
                display: none;
            }
        }

        .text-box {
            max-width: none;
        }

        .breadcrumb-holder {
            padding: 0;

        }

        .breadcrumb {
            letter-spacing: 0.1em;

            @media (min-width: $widescreen + 1) {
                font-size: 28px;
                margin: 0 -9px 0 -25px;
            }

            >li {
                @media (min-width: $widescreen + 1) {
                    padding: 0 9px 0 25px;
                }

                >a {
                    color: inherit;

                    &:hover {
                        color: $secondary;
                    }
                }
            }
        }
    }
}


/* Section What We Do */
.section-whatwedo {

    .section-head {
        text-align: center;

        .sign-line {
            margin-left: auto;
            margin-right: auto;
        }
    }

    .tabs-listing-wrap {
        overflow: hidden;
        overflow-x: auto;
        margin: 0 0 20px;
    }

    .tabs-listing {
        padding: 0 0 20px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        margin: 0;
        color: #343434;
        list-style: none;
        min-width: 650px;
        font: 600 26px/1.27 "Poppins", Arial;

        @media (max-width: $widescreen) {
            font-size: 20px;
        }

        @media (max-width: $desktop) {
            font-size: 15px;
        }

        @media (max-width: 650px) {
            justify-content: flex-start;
        }

        li {
            position: relative;
            padding: 0 5px;
            flex-shrink: 0;

            &.tabActive>a {
                border-color: $primary;
            }

            a {
                display: block;
                padding: 10px 20px;
                color: inherit;
                border-bottom: 3px solid transparent;

                @media (max-width: $widescreen) {
                    padding-left: 13px;
                    padding-right: 13px;
                }

                &:hover {
                    border-color: $primary;
                    text-decoration: none;
                }
            }
        }
