    .tab-content {
        position: relative;
    }

    .features-item {
        display: flex;
        flex-flow: row wrap;
        font-size: 16px;
        margin: -40px -10px 0;

        @media (max-width: $widescreen) {
            margin-top: -20px;
        }

        @media (max-width: $desktop) {
            font-size: 14px;
        }

        .item-box {
            width: 33.33%;
            padding: 40px 10px 0;
            display: flex;

            @media (max-width: $widescreen) {
