            }

            @media (max-width: $tablet) {
                width: 100%;
            }

            .item-holder {
                width: 100%;
                border-radius: 4px;
                padding: 30px;
                border: 1px solid #CBCBCB;

                @media (max-width: $widescreen) {
                    padding: 20px;
                }
            }

            .ico-holder {
                height: 80px;
                display: flex;
                align-items: center;
                margin: 0 0 20px;

                img {
                    max-height: 100%;
                    width: auto;
                }
            }

            h5 {
                margin: 0 0 12px;

                @media (min-width: $widescreen + 1) {
                    font-size: 20px;
                }
            }
        }
    }
}

/* Section Figures */
.section-figures {
    .figures-block {
        display: flex;
        flex-flow: row wrap;
        margin: -112px -20px 0;

        @media (max-width: $widescreen) {
            margin-top: -70px;
        }

        @media (max-width: $desktop) {
            margin: -40px -10px 0;
        }
    }

    .box-figure {
        width: 50%;
        padding: 112px 20px 0;
        display: flex;
        align-items: center;

        @media (min-width: $widescreen + 1) {
            font-size: 20px;
        }

        @media (max-width: $widescreen) {
            padding-top: 70px;
        }

        @media (max-width: $desktop) {
            padding: 40px 10px 0;
            width: 100%;
        }

        .figure-holder {
            flex-shrink: 0;
            margin: 0 30px;
            width: 137px;
            height: 137px;
            color: $white;
            display: flex;
            position: relative;
            align-items: center;
            z-index: 50;
            justify-content: center;
            padding: 10px;
            font: 40px/1.28 "Poppins", Arial;

            @media (max-width: $widescreen) {
                font-size: 30px;
                width: 120px;
                height: 120px;
            }

            @media (max-width: $desktop) {
                font-size: 25px;
                margin: 0 22px;
                width: 100px;
                height: 100px;
            }

            &:after {
                position: absolute;
                left: 0;
                top: 0;
                border-radius: 4px;
                right: 0;
                bottom: 0;
                content: "";
                background: $primary;
                z-index: -1;
            }

            &:before {
                position: absolute;
                left: -30px;
                bottom: -30px;
                z-index: -2;
                width: 89px;
                height: 89px;
                border-radius: 50%;
                content: "";
                background: #a2a2a2;

                @media (max-width: $desktop) {
                    width: 65px;
                    left: -22px;
                    bottom: -22px;
                    height: 65px;
                }

            }

            strong {
                display: block;
                font-weight: 600;
            }
        }

        .text-box {
            flex-grow: 1;
            overflow: hidden;
        }

        h4 {
            margin: 0 0 16px;
            color: #343434;

            @media (max-width: $widescreen) {
                margin: 0 0 12px;
            }
        }
    }

    &.figures-modified {
        .box-figure {
            width: 25%;
            display: block;
            text-align: center;

            @media (max-width: $desktop) {
                width: 50%;
            }

            @media (max-width: $tablet) {
                width: 100%;
            }

            .figure-holder {
                margin: 0 auto 67px;

                @media (min-width: $widescreen + 1) {
                    width: 126px;
                    height: 126px;
                }
            }
        }
    }
}

/* Section Clients */
.section-clients {
    position: relative;
    text-align: center;

    h2 {
        .sign-line {
            margin-left: auto;
            margin-right: auto;
        }
    }

    .clients-list {
        display: flex;
        flex-flow: row wrap;
        margin: -80px -30px 0;
        padding: 0;
        list-style: none;
        justify-content: center;

        @media (max-width: $widescreen) {
            margin: -50px -20px 0;
        }

        @media (max-width: $tablet) {
            margin: -30px -10px 0;
        }

        >li {
            padding: 80px 30px 0;
            min-width: 20%;

            @media (max-width: $widescreen) {
                padding: 50px 20px 0;
                width: 20%;
            }

            @media (max-width: $desktop) {
                width: 33.33%;

            }

            @media (max-width: $tablet) {
                width: 50%;
                padding: 30px 10px 0;
            }

            img {
                display: block;
                margin: 0 auto;
            }
        }
    }

    &.section-awards {
        .clients-list {

            @media (min-width: $widescreen + 1) {
                margin-top: -60px;
            }

            >li {


                @media (min-width: $desktop + 1) {
                    width: 25%;
                    padding-top: 60px;
                }
            }
        }
    }
}

/* Section Life */

.section-life {
    overflow: hidden;

    .grid-items {
        margin: -10px;
    }

    .isotope {
        position: relative;
    }

    .grid-sizer {
        width: 33.33%;
    }

    .item-grid {
        width: 33.33%;
        float: left;
        height: 321px;
        padding: 10px;

        &.portrait-large {
            height: 442px;
        }

        &.portrait-small {
            height: 266px;
        }

        &.landscape {
            width: 66.66%;
            height: 388px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

/* Breadcrumb Styles */
.breadcrumb-holder {
    position: relative;
    padding: 150px 0 15px;

    @media (max-width: $widescreen) {
        padding-top: 120px;
    }

    @media (max-width: $desktop) {
        padding-top: 80px;
    }

    @media (max-width: $tablet) {
        padding-top: 40px;
    }

    .breadcrumb-frame {
        position: relative;
        overflow: hidden;
        display: inline-block;
        vertical-align: top;
        max-width: 100%;
    }
}

.breadcrumb {
    margin: 0 -6px 0 -17px;
    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: row wrap;
    font: 600 20px/1.28 "Poppins", Arial;

    @media (max-width: $widescreen) {
        font-size: 18px;
    }

    @media (max-width: $desktop) {
        font-size: 16px;
    }

    >li {
        padding: 0 6px 0 17px;
        position: relative;

        &:before {
            position: absolute;
            left: 0;
            top: 0;
            content: ">";
        }

        >a {
            display: block;
            color: $secondary;

            &:hover {
                text-decoration: none;
                color: $primary;
            }
        }
    }
}

/* Service Details */
.service-details {
    position: relative;

    .img-holder {
        margin: 0 0 40px;

        @media (max-width: $widescreen) {
            margin: 0 0 20px;
        }

        img {
            width: 100%;
            border-radius: 4px;
        }
    }

    h4 {
        color: $primary;
    }

    .img-area {
        max-width: 50%;

        &.img-align-right {
            float: right;
            margin: 0 0 0 30px;

            @media (max-width: $desktop) {
                float: none;
                margin: 0 auto;
                padding: 0 0 30px;
            }
        }

        &.img-align-left {
            float: left;
            margin: 0 30px 0 0;

            @media (max-width: $desktop) {
                float: none;
                margin: 0 auto;
                padding: 0 0 30px;
            }
        }
    }

    p {
        @media (min-width: $widescreen + 1) {
            margin: 0 0 40px;
        }
    }
}

/* Service Detail Widgets */
.page-service {
    .widget {
        padding: 30px 0;
        border: 0;

        &:first-child {
            padding-top: 0;
        }

        &:last-child {
            padding-bottom: 0;
        }

        h5 {
            text-transform: capitalize;
            margin: 0 0 12px;
            color: $secondary;

            @media (min-width: $widescreen + 1) {
                font-size: 20px;
            }
        }

        .btn-sm {
            display: flex;
            align-items: center;
            padding: 13px 20px;
            min-width: inherit;
            letter-spacing: 0;
