    }

    .ico-call {
        width: 90px;
        height: 90px;
        background: $white;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 10px solid #92CAFF;
        margin: 0 auto 40px;
    }

    h4 {
        margin: 0 0 40px;
