        color: $white;
        position: relative;

        @media (max-width: $widescreen) {
            margin: 0 0 30px;
        }

        @media (max-width: $desktop) {
            margin: 0 0 20px;
        }

        @media (min-width: $widescreen + 1) {
            font-size: 28px;
        }

        &:before {
            position: absolute;
            right: 50px;
            top: -60px;
            width: 33px;
            height: 41px;
            content: "";
            background: url(../images/arrow-call-2.svg) no-repeat;
        }
    }

    .title {
        display: block;
        font: 600 16px/1.28 "Poppins", Arial;
        margin: 0 0 8px;
    }

    .phone {
        display: block;
        color: $white;
        font: 700 28px/1.28 "Poppins", Arial;

        @media (max-width: $widescreen) {
            font-size: 22px;
        }

        a {
            color: inherit;

            &:hover {
                color: $secondary;
                text-decoration: none;
            }
        }
    }
}

/* Widget Quote */
.widget-quote {
    .widget-holder {
        padding: 60px 40px;
        background: #F1F4FA;
        border-radius: 4px;

        @media (max-width: $widescreen) {
            padding: 20px;
        }

        h5 {
            margin: 0 0 45px;

            @media (max-width: $widescreen) {
                margin: 0 0 20px;
            }
        }
    }



    .input-holder {
        padding: 0 0 20px;
        margin: 0;

        label {
            display: block;
            margin: 0 0 12px;
            color: #343434;
            font: 600 16px/1.28 "Poppins", Arial;
        }

        .form-control {
            height: 48px;
            background: $white;
            padding: 16px 10px;
            width: 100%;
            font-size: 12px;
            font-weight: 400;
            line-height: 16px;
            margin: 0;

            &::placeholder {
                color: #959595;
            }

            &:-ms-input-placeholder {
                color: #959595;
            }

            &::-ms-input-placeholder {
                color: #959595;
            }
        }

        textarea {
            height: 112px !important;
        }
    }

    .btn-primary {
        display: block;
        margin: 20px 0 0;
    }

    .btn {
        width: 100%;
        box-shadow: none;
    }
}

/* Widget Textbox */
.widget-textbox {
    font-size: 14px;

    .widget-holder {
        padding: 40px;
        box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        background: #F1F4FA;

        @media (max-width: $widescreen) {
            padding: 20px;
        }
    }

    p:not(:last-child) {
        margin: 0 0 16px;
    }
}


.accContent {
    display: none;
}

/* Section Faqs */
.section-faqs {
    .accordion-faqs {
        margin: 0;
        padding: 0;
        list-style: none;

        .accordion-frame {
            padding: 0 0 35px;

            @media (max-width: $widescreen) {
                padding: 0 0 20px;
            }
        }

        li {
            border-bottom: 1px solid rgba(#6D6D6D, .5);

            &.accActive {
                h4 {
                    color: $secondary;

                    .ico-plus:before {
                        background: $secondary;
                    }

                    .ico-plus:after {
                        opacity: 0;
                    }
                }
            }

            h4 {
                color: #343434;
                font-size: 26px;
                font-weight: 500;
                padding: 35px 0;
                margin: 0;

                @media (max-width: $widescreen) {
                    padding: 20px 0;
                    font-size: 22px;
                }

                br {
                    @media (max-width: $widescreen) {
                        display: none;
                    }
                }

                @media (max-width: $tablet) {
                    font-size: 17px;
                }

                a {
                    display: flex;
                    align-items: flex-start;
                    position: relative;

                    &:hover {
                        text-decoration: none;
                        color: $secondary;
                    }

                    span {
                        flex-grow: 1;
                        overflow: hidden;
                    }
                }
            }

            .ico-plus {
                flex-shrink: 0;
                margin: 0 0 0 15px;
                position: relative;
                width: 20px;
                height: 20px;

                &:after,
                &:before {
                    position: absolute;
                    left: 50%;
                    top: 0;
                    bottom: 0;
                    width: 4px;
                    transform: translateX(-50%);
                    content: "";
                    transition: all .25s ease-in-out;
                    background: #343434;
                    border-radius: 2px;
                }

                &:before {
                    top: 50%;
                    transform: translateY(-50%);
                    width: auto;
                    left: 0;
                    right: 0;
                    height: 4px;
                    top: 50%;
                    bottom: auto;
                }
            }
        }
    }
}

/* Section Team */
.section-team {
    .team-members {
        position: relative;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        margin: -40px -10px 0;
    }

    .member-box {
        display: flex;
        padding: 40px 10px 0;
        width: 25%;
        text-align: center;

        @media (max-width: $desktop) {
            width: 33.33%;
        }

        @media (max-width: $tablet) {
            width: 50%;
        }

        @media (max-width: $phone) {
            width: 100%;
        }

        .box-holder {
            width: 100%;
            background: #FAF9F5;
            padding: 20px 20px 45px;
            border-radius: 8px;

            @media (max-width: $widescreen) {
                padding: 15px 10px;
            }

        }

        .img-holder {
            border-radius: 8px;
            overflow: hidden;
            height: 240px;

            img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: 50% 0;
            }
        }

        .text-box {
            padding: 20px 0 0;
        }

        h5 {
            font-size: 20px;
            margin: 0 0 8px;

            @media (max-width: $widescreen) {
                font-size: 16px;
            }
        }

        h6 {
            font-size: 16px;
            font-weight: 400;

            @media (max-width: $widescreen) {
                font-size: 13px;
            }
        }
    }

    &.bg-gray {
        .member-box .box-holder {
            background: #E3E3E3;
        }
    }
}

/* Section Video */
.section-video {
    text-align: center;

    video {
        max-width: 100%;
        border: 0;
        display: inline-block;
        vertical-align: top;
        height: auto;

    }
}

/* Section Goals */
.section-goals {
    .section-head {
        text-align: center;
        margin-bottom: 40px;
    }
}

.featured-boxes {
    display: flex;
    margin: -24px -12px 0;
    flex-flow: row wrap;

    .featured-box {
        width: 33.33%;
        padding: 24px 12px 0;
        display: flex;

        @media (max-width: $desktop) {
            width: 100%;
        }

        .box-frame {
            width: 100%;
            background: $white;
            box-shadow: 0px 3px 14px rgba(0, 0, 0, 0.16);
            border-radius: 12px;
            padding: 40px 20px;
        }

        .ico-holder {
            display: flex;
            align-items: center;
            min-height: 80px;
            margin: 0 0 20px;
        }

        h5 {
            margin: 0 0 18px;
        }
    }
}